.profile__info-job {
    margin: 0;
    font-size: 18px;
    line-height: 20px;
    padding-top: 10px;
    text-overflow: ellipsis;
    color: white;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 900px) {
    .profile__info-job {
        text-align: center;
    }
}

@media screen and (max-width: 768px) {
    .profile__info-job {
        font-size: 14px;
        line-height: 17px;
        padding-top: 0;
    }
}