.root {
    max-width: 882px;
    min-width: 282px;
    margin: 0 auto;
    padding: 0;
    font-family: "Inter", "Arial", sans-serif;
    background-color: #000;
    color: #fff;
}

@media screen and (max-width: 1024px) {
    .root {
        padding-right: 24px;
        padding-left: 24px;
        padding-bottom: 56px;
    }
}

@media screen and (max-width: 768px) {
    .root {
        padding-right: 25px;
        padding-left: 25px;
        padding-bottom: 36px;
    }
}

@media screen and (max-width: 425px) {
    .root {
        padding-right: 24px;
        padding-left: 24px;
    }
}

@media screen and (max-width: 320px) {
    .root {
        padding-right: 0;
        padding-left: 0;
    }
}